nav ul li:not(:last-child) {
  position: relative;
}

nav ul li:not(:last-child) a:after {
  content: "";
  height: 20px;
  width: 2px;
  background: white;
  display: block;
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -10px; /* half of your height */
  z-index: 999;
}

