.video-header{
    top: 15%;
    right: 0;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 6;
    margin-right: auto!important;
    margin-left: auto !important;
}

.responsive-video {
    width: 100% !important;
    height: auto;
}

.parallelogram-video{
    width: 100%    !important;
    height: auto   !important;
    /*transform: skew(-10deg);*/
    /*transform: ;*/
    -webkit-transform-origin: top left;
        -ms-transform-origin: top left;
            transform-origin: top left;
    -webkit-transform: skewX(-20deg);
    -ms-transform: skewX(-20deg);
    transform: skewX(-20deg);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.source{
    display:inline-block;
    -webkit-transform-origin: top left;
        -ms-transform-origin: top left;
            transform-origin: top left;
    -webkit-transform: skewX(20deg);
        -ms-transform: skewX(20deg);
            transform: skewX(20deg);
}

/*VIDEO HOMEPAGE BG*/
.homepage-hero-module {
    border-right: none;
    border-left: none;
    position: relative;
    height: auto !important;
}

.no-video .video-container video,
.touch .video-container video {
    display: none;
}

.no-video .video-container .poster,
.touch .video-container .poster {
    display: block !important;
}

.video-container {
    position: relative;
    bottom: 0%;
    left: 0%;
    height: 100%;
    width: 100%;
    overflow: hidden;
    background: #000;
}

.video-container .poster img {
    width: 100%;
    bottom: 0;
    position: absolute;
}

.video-container .filter {
    z-index: 2;
    position: absolute;
    background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(0,0, 0,.77)),to(rgba(0,0,0,.44)));
    background-image: -webkit-linear-gradient(top,rgba(0,0, 0,.77),rgba(0,0,0,.44));
    background-image: -o-linear-gradient(top,rgba(0,0, 0,.77),rgba(0,0,0,.44));
    background-image: linear-gradient(to bottom,rgba(0,0, 0,.77),rgba(0,0,0,.44));
    width: 100%;
    top: 0;
    bottom: 0;
}

.video-container video {
    position: absolute;
    z-index: 0;
    bottom: 0;
}

.video-container video.fillWidth {
    width: 100%;
}

/*VIDEO HOMEPAGE BG END*/

/*SLICK VIDEO CAROUSEL*/

.prev-arrow{
    position: absolute;
    z-index: 99;
    bottom: 40%;
    right: 30%;
}
.next-arrow{
    position: absolute;
    z-index: 99;
    bottom: 40%;
    right: 20%;
}

.slick-slider{
    padding-bottom: 40px;
}

/*.slick-slide{*/
/*padding: 20px;*/
/*}*/

.slider-wr {
    /*width: 80%;*/
    margin: 20px auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.slick-slide {
    padding: 0 8px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.slick-slide .video-container-carousel {
    max-width: 100%;
    height: auto;
    position: relative;
    -webkit-transform: scale(0.9);
        -ms-transform: scale(0.9);
            transform: scale(0.9);
    -webkit-transition: all 1s ease-out;
    -o-transition: all 1s ease-out;
    transition: all 1s ease-out;
    opacity: 0.5;
}

div.slick-slide.slick-active.slick-center .video-container-carousel {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition: all 1s ease-out;
    -o-transition: all 1s ease-out;
    transition: all 1s ease-out;
    opacity: 1;
}

.video-container-carousel{
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
}

/*SLICK VIDEO CAROUSEL END*/

