.playpause {
    background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(23, 145, 211, .4)),to(rgba(23, 145, 211, .4)));
    background-image: -webkit-linear-gradient(top,rgba(23, 145, 211, .4),rgba(23, 145, 211, .4));
    background-image: -o-linear-gradient(top,rgba(23, 145, 211, .4),rgba(23, 145, 211, .4));
    background-image: linear-gradient(to bottom,rgba(23, 145, 211, .4),rgba(23, 145, 211, .4));
    /*background-image:url(http://png-4.findicons.com/files/icons/2315/default_icon/256/media_play_pause_resume.png);*/
    background-repeat:no-repeat;
    /*width:50%;*/
    /*height:50%;*/
    position:absolute;
    left:0%;
    right:0%;
    top:0%;
    bottom:1%;
    margin:0 auto;
    background-size:contain;
    background-position: center;
    z-index: 1;
    -webkit-transform-origin: top left;
        -ms-transform-origin: top left;
            transform-origin: top left;
    -webkit-transform: skewX(-20deg);
    -ms-transform: skewX(-20deg);
    transform: skewX(-20deg);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.fa-play-circle-o{
    position: relative;
    /*right: 50%;*/
    z-index: 2;
    -webkit-transform: skewX(20deg);
        -ms-transform: skewX(20deg);
            transform: skewX(20deg);
    text-align: center;
}



